<template>
  <div class="home">
    <div class="login">
      <a
        href="https://account.kkbox.com/oauth2/authorize?redirect_uri=https%3a%2f%2fguoyanting0919.github.io%2fkkboxweb&client_id=fb254aff20b31017a2e84e34ed896523&response_type=code&state=123"
        class="wasNotLoginContainer"
        v-if="!getCode"
      >
        <i class="fas fa-user wasNotLogin"></i>
        <a class="spanRed">未登入</a>
        <p class="to_open">
          登入開啟
          <br />更多功能
        </p>
      </a>

      <a class="wasLoginContainer" v-else>
        <i class="fas fa-user wasLogin"></i>
        <a class="spanGreen">已登入</a>
        <p class="get_favorite" @click="getMyFavoriteTrack">
          取得我的
          <br />蒐藏歌曲
        </p>
      </a>
    </div>
    <!-- bgRotate -->
    <div>
      <div v-if="searchItems" class="bgRotate"></div>
    </div>
    <!-- iframe -->
    <myIframe
      :kkboxIframeId="iframeId"
      :youtubeIframeId="YTTrackId"
      :youtubeSearchName="YTSearchName"
    ></myIframe>

    <!-- navBar -->
    <navBar
      @setSearchInput="getSearchInput"
      @setSearchType="getsearchType"
      @setsearchItems="getsearchItems"
      @setkkboxData="getkkboxData"
      @setIsLoading="getIsLoading"
    ></navBar>

    <!-- isLoading -->
    <isLoading v-if="isLoading"></isLoading>

    <router-view
      :myKkboxData="kkboxData"
      :mysearchItems="searchItems"
      :myAccessToken="accessToken"
      :myCode="getCode"
      @setIframeId="getIframeId"
      @setYTTrackId="getYTTrackId"
      @setIsLoading="getIsLoading"
    ></router-view>
  </div>
</template>

<script>
import navBar from "./components/navBar";
import myIframe from "./components/iframe";
import isLoading from "./components/isLoading";
export default {
  name: "index",
  components: {
    navBar,
    myIframe,
    isLoading
  },
  data() {
    return {
      token: "",
      kkboxData: "",
      searchInput: "",
      searchType: "",
      kkboxCharts: [],
      artistID: "",
      searchItems: "",
      iframeId: "",
      iframeType: "song",
      YTSearchName: "",
      YTTrackId: "",
      isLoading: true,
      accessToken: "",
      userId: ""
    };
  },
  computed: {
    getCode() {
      let params = new URL(document.location).searchParams;
      let code = params.get("code");
      return code;
    }
  },
  methods: {
    getAccessToken() {
      const vm = this;
      if (!vm.getCode) {
        console.log("noCODE");
        return false;
      } else {
        const api =
          "https://cors-anywhere.herokuapp.com/https://account.kkbox.com/oauth2/token";
        const config = {
          headers: {
            Accept: "application/x-www-form-urlencoded",
            "Content-Type": "application/x-www-form-urlencoded"
          }
        };
        const oauth = {
          grant_type: "authorization_code",
          code: this.getCode,
          client_id: "fb254aff20b31017a2e84e34ed896523",
          client_secret: "d2f7853d7a7e2afd98bc4751f1b3c81a"
        };
        this.$http.post(api, this.$qs.stringify(oauth), config).then(res => {
          vm.accessToken = res.data.access_token;
        });
      }
    },
    getMyFavoriteTrack() {
      const vm = this;
      let path = vm.$router.history.current.path;
      if (path === "/track") {
        vm.isLoading = true;
        const api = "https://api.kkbox.com/v1.1/me/favorite?limit=100";
        const user = {
          headers: {
            Authorization: ` Bearer ${vm.accessToken}`
          }
        };
        vm.$http.get(api, user).then(res => {
          vm.searchItems = res.data.data;
          vm.isLoading = false;
        });
      } else {
        vm.activeRoute = "單曲搜尋";
        vm.$router.push("/track");
        const api = "https://api.kkbox.com/v1.1/me/favorite?limit=100";
        const user = {
          headers: {
            Authorization: ` Bearer ${vm.accessToken}`
          }
        };
        vm.$http.get(api, user).then(res => {
          vm.searchItems = res.data.data;
        });
      }
    },
    getSearchInput(i) {
      this.searchInput = i;
    },
    getsearchType(i) {
      this.searchType = i;
    },
    getsearchItems(i) {
      this.searchItems = i;
    },
    getkkboxData(i) {
      this.kkboxData = i;
    },
    getIframeId(i) {
      this.iframeId = i;
    },
    getYTTrackId(i) {
      this.YTTrackId = i;
    },
    getIsLoading(i) {
      this.isLoading = i;
    },

    getCharts() {
      let vm = this;
      const config = {
        headers: {
          Authorization: ` Bearer ${vm.token}`
        }
      };
      this.$http
        .get(`https://api.kkbox.com/v1.1/charts?territory=TW`, config)
        .then(res => {
          console.log(res);
          vm.kkboxCharts = res.data.data;
        });
    }
  },
  created() {
    this.getSearchInput();
    this.getsearchItems();
    this.getAccessToken();
  }
};
</script>

