<template>
  <div class="tracks_container">
    <div class="track_list">
      <ul class="track">
        <li
          v-for="(item,index) in searchItems"
          :key="index"
          class="track_name"
          :class="{'YTcurrentTrack':YTcurrentTrack ===item.id,'KKcurrentTrack':KKcurrentTrack ===item.id}"
          :data-text="item.name"
        >
          {{item.name}}
          <div class="play_btn_container">
            <button class="playBtn playYoutube" @click="playYT(item)">
              <i class="fab fa-youtube track_YT_icon"></i>
            </button>
            <button class="playBtn playKKbox" @click="playKkbox(item)">
              <i class="fab fa-kickstarter track_KK_icon"></i>
            </button>
            <button v-if="code" class="playBtn playKKbox" @click="addToFavorite(item)">
              <i class="far fa-star favorite_icon"></i>
            </button>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ["myKkboxData", "mysearchItems", "myCode"],
  data() {
    return {
      token: "",
      kkboxData: this.myKkboxData,
      searchItems: this.mysearchItems,
      accessToken: "",
      code: this.myCode,
      iframeId: "",
      YTTrackId: "",
      YTcurrentTrack: "",
      KKcurrentTrack: ""
    };
  },
  watch: {
    myKkboxData(val) {
      this.kkboxData = val;
    },
    mysearchItems(val) {
      this.searchItems = val;
    },
    myCode(val) {
      this.code = val;
    }
  },
  computed: {
    creatItem() {
      let num = Math.floor(Math.random() * 4);
      let creatItem = this.searchItems[num];
      return creatItem;
    }
  },
  methods: {
    playKkbox(item) {
      const vm = this;
      vm.YTcurrentTrack = "";
      vm.KKcurrentTrack = "";
      vm.YTTrackId = "";
      vm.iframeId = "";
      vm.$emit("setIframeId", vm.iframeId);
      vm.$emit("setYTTrackId", vm.YTTrackId);
      vm.KKcurrentTrack = item.id;
      setTimeout(function() {
        vm.iframeId = item.id;
        vm.$emit("setIframeId", vm.iframeId);
        let KK_iframe_container = document.querySelector(
          ".KK_iframe_container"
        );
        KK_iframe_container.style.opacity = 0.9;
      }, 10);
    },
    playYT(item) {
      const vm = this;
      vm.YTcurrentTrack = "";
      vm.KKcurrentTrack = "";
      vm.iframeId = "";
      vm.YTSearchName = "";
      vm.YTTrackId = "";
      vm.$emit("setIframeId", vm.iframeId);
      vm.$emit("setYTTrackId", vm.YTTrackId);
      vm.YTcurrentTrack = item.id;
      setTimeout(function() {
        vm.YTSearchName = item.name;
        let TY_iframe_container = document.querySelector(
          ".TY_iframe_container"
        );
        TY_iframe_container.style.opacity = 0.9;
        vm.$http
          .get(
            `https://www.googleapis.com/youtube/v3/search?key=AIzaSyD5X4wGGRG0IyiFPYt66UXKAg-An0ystvQ&part=snippet&type=video&q=${vm.YTSearchName}`
          )
          .then(res => {
            vm.YTTrackId = res.data.items[0].id.videoId;
            vm.$emit("setYTTrackId", vm.YTTrackId);
          });
      }, 100);
    },
    addToFavorite(item) {
      const vm = this;
      let trackid = item.id;
      const FaAutho = {
        headers: {
          Authorization: ` Bearer ${vm.accessToken}`,
          "Content-Type": "application/json"
        }
      };
      const faTrackId = { track_id: item.id };
      vm.$http
        .post(
          "https://cors-anywhere.herokuapp.com/https://api.kkbox.com/v1.1/me/favorite",
          faTrackId,
          FaAutho
        )
        .then(res => {
          alert(res.data.message);
        });
    },
    getAccessToken() {
      const vm = this;
      if (!vm.code) {
        console.log("noCODE");
        return false;
      } else {
        const api =
          "https://cors-anywhere.herokuapp.com/https://account.kkbox.com/oauth2/token";
        const config = {
          headers: {
            Accept: "application/x-www-form-urlencoded",
            "Content-Type": "application/x-www-form-urlencoded"
          }
        };
        const oauth = {
          grant_type: "authorization_code",
          code: this.code,
          client_id: "fb254aff20b31017a2e84e34ed896523",
          client_secret: "d2f7853d7a7e2afd98bc4751f1b3c81a"
        };
        this.$http.post(api, this.$qs.stringify(oauth), config).then(res => {
          vm.accessToken = res.data.access_token;
        });
      }
    }
  },
  mounted() {
    const vm = this;
    this.getAccessToken();
    vm.isLoading = true;
    vm.$emit("setIsLoading", vm.isLoading);
    window.setTimeout(function() {
      vm.isLoading = false;
      vm.$emit("setIsLoading", vm.isLoading);
    }, 1500);
    window.setTimeout(function() {
      let once = vm.creatItem;
      vm.playKkbox(once);
    }, 3000);
  }
};
</script>

<style scope>
@import url("../assets/tracks_container_test.css");
@import url("../assets/tracks_container_RWD.css");
</style>